html, body, div, span, applet, object, iframe, h1, h2, h3, h4, h5,
h6, p, blockquote, pre, a, abbr, acronym, address, big,
cite, code, del, dfn, em, img, ins, kbd, q, s, samp, small,
strike, strong, sub, sup, tt, var, b, u, i, center, dl, dt,
dd, ol, ul, li, fieldset, form, label, legend, table, caption,
tbody, tfoot, thead, tr, th, td, article, aside, canvas, details,
embed, figure, figcaption, footer, header, hgroup, menu, nav,
output, ruby, section, summary, time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  vertical-align: baseline;
}

html {
  -ms-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%;
}

img, fieldset, a img {
  border: none;
}

input[type="text"],
input[type="email"],
input[type="tel"],
textarea {
  -webkit-appearance: none;
}

input[type="submit"],
button {
  cursor: pointer;
}

input[type="submit"]::-moz-focus-inner,
button::-moz-focus-inner {
  padding: 0;
  border: 0;
}

textarea {
  overflow: auto;
}

input, button {
  margin: 0;
  padding: 0;
  border: 0;
}

div, input, textarea, select, button,
h1, h2, h3, h4, h5, h6, a, span, a:focus {
  outline: none;
}

ul, ol {
  list-style-type: none;
}

table {
  border-spacing: 0;
  border-collapse: collapse;
  width: 100%;
}

html {
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
}

@media (prefers-reduced-motion: reduce) {
  * {
    transition: none !important;
  }
}

.icon-arrow {
  width: 0.69em;
  height: 1em;
  fill: #FFF;
}

.icon-facebook {
  width: 0.53em;
  height: 1em;
  fill: #000;
}

@font-face {
  font-family: "bebas";
  src: url("../fonts/bebas-webfont.woff2") format("woff2"), url("../fonts/bebas-webfont.woff") format("woff");
  font-weight: normal;
  font-style: normal;
  font-display: swap;
}

a {
  text-decoration: none;
}

body {
  font-family: bebas,sans-serif;
  font-weight: 100;
}

h4 {
  font-size: 12px;
  color: #e8603c;
}

p {
  font-family: Helvetica Neue, sans-serif;
  font-size: 12px;
  line-height: 18px;
}

h2 {
  font-size: 18px;
  color: #e8603c;
}

.out {
  overflow: hidden;
  margin-left: -40px;
}

.container {
  margin: 0 auto;
  max-width: 940px;
}

.content {
  display: flex;
  margin-top: 23px;
}

.header {
  font-family: bebas,sans-serif;
  font-weight: 100;
  margin-bottom: 17px;
  margin-top: 14px;
}

.header__container {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.logo {
  pading-left: 10px;
  font-size: 36px;
  display: flex;
}

.logo__left-part {
  color: #e8603c;
}

.logo__right-part {
  color: #f3af9d;
}

.nav__link {
  color: #b9b9b9;
}

.nav__link:not(:last-child) {
  margin-right: 28px;
}

.nav__link:hover {
  color: #e8603c;
}

.is-active {
  color: #e8603c;
}

.page-title {
  position: relative;
}

.page-title:before, .page-title:after {
  position: absolute;
  height: 60px;
  background: #f2ae9c;
  content: " ";
  width: 1000px;
  top: 0;
}

.page-title:before {
  right: 100%;
  margin-right: 10px;
}

.page-title:after {
  left: 100%;
  margin-left: 10px;
}

.page-title h1 {
  color: #fff;
  background-color: #e8603c;
  padding-left: 17px;
  height: 60px;
  vertical-align: middle;
  padding-top: 12px;
  font-size: 24px;
}

.post {
  width: 71.2766%;
  margin-right: 50px;
}

.post__container {
  position: relative;
  margin-bottom: 55px;
}

.post__icon {
  position: absolute;
  margin-left: -85px;
  top: 0;
  left: 0;
}

.post__inner {
  display: flex;
  margin-top: 24px;
}

.post__informers {
  width: 20.89552%;
}

.post__informers h4 {
  padding-bottom: 10px;
}

.post__informers p {
  padding-bottom: 20px;
  color: #c3c3c3;
}

.post__content {
  width: 79.10448%;
}

.title {
  padding-bottom: 23px;
}

.icon-block {
  height: 75px;
  width: 75px;
  background-color: red;
}

.quote__inner {
  display: flex;
  background-size: cover;
}

.quote__text {
  padding-top: 25px;
  padding-bottom: 23px;
}

.quote h2 {
  font-size: 72px;
  font-family: Georgia, serif;
}

.quote p {
  font-size: 13px;
  font-style: italic;
}

.slider {
  position: relative;
}

.slider__slide {
  background-size: cover;
  height: 0;
  padding-bottom: 46%;
}

.slider__next, .slider__prev {
  font-size: 50px;
  position: absolute;
  top: 50%;
  left: 19px;
  cursor: pointer;
  z-index: 2;
  margin-top: -36px;
}

.slider__next {
  left: auto;
  right: 20px;
  transform: rotate(-180deg);
}

/* Slider */
.slick-slider {
  position: relative;
  display: block;
  box-sizing: border-box;
  -moz-box-sizing: border-box;
  -webkit-touch-callout: none;
  -webkit-user-select: none;
  -khtml-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
  -ms-touch-action: pan-y;
  touch-action: pan-y;
  -webkit-tap-highlight-color: transparent;
}

.slick-list {
  position: relative;
  overflow: hidden;
  display: block;
  margin: 0;
  padding: 0;
}

.slick-list:focus {
  outline: none;
}

.slick-list.dragging {
  cursor: pointer;
  cursor: hand;
}

.slick-slider .slick-track,
.slick-slider .slick-list {
  -webkit-transform: translate3d(0, 0, 0);
  -moz-transform: translate3d(0, 0, 0);
  -ms-transform: translate3d(0, 0, 0);
  -o-transform: translate3d(0, 0, 0);
  transform: translate3d(0, 0, 0);
}

.slick-track {
  position: relative;
  left: 0;
  top: 0;
  display: block;
}

.slick-track:before, .slick-track:after {
  content: "";
  display: table;
}

.slick-track:after {
  clear: both;
}

.slick-loading .slick-track {
  visibility: hidden;
}

.slick-slide {
  float: left;
  height: 100%;
  min-height: 1px;
  display: none;
}

[dir="rtl"] .slick-slide {
  float: right;
}

.slick-slide img {
  display: block;
}

.slick-slide.slick-loading img {
  display: none;
}

.slick-slide.dragging img {
  pointer-events: none;
}

.slick-initialized .slick-slide {
  display: block;
}

.slick-loading .slick-slide {
  visibility: hidden;
}

.slick-vertical .slick-slide {
  display: block;
  height: auto;
  border: 1px solid transparent;
}
