@include font('bebas',bebas-webfont);

@mixin bebas {
	font-family: bebas,sans-serif;
	font-weight: 100; }
@mixin helvetica {
	font-family: Helvetica Neue, sans-serif; }
a {
	text-decoration: none; }
body {
	@include bebas; }
h4 {
	font-size: 12px;
	color: #e8603c; }
p {
	@include helvetica;
	font-size: 12px;
	line-height: 18px; }
h2 {
	font-size: 18px;
	color: #e8603c; }
